import { Component, OnInit } from '@angular/core';
import {FormControl} from "@angular/forms";

@Component({
  selector: 'app-service-catalog',
  templateUrl: './service-catalog.component.html',
  styleUrls: ['./service-catalog.component.css']
})
export class ServiceCatalogComponent implements OnInit {
    private _current = 1;
    // 
    public title: String = '';
    // 
    private tabs = [
        {
            index: 1,
            name: '',
            tabName: 'Middleware',
            disabled: false
        },
        {
            index: 2,
            name: '',
            tabName: 'Database',
            disabled: false
        },
        {
            index: 3,
            name: '',
            tabName: 'Microservice',
            disabled: false
        }
    ];
  // 
  private serviceType = ['Microservice', 'Database', 'Middleware'];
    // 
    catalogOption = [
        { label: '', value: 'tomcat', checked: true },
        { label: '', value: 'db' },
    ];
    internetOption = [
        { label: 'portal', value: 'publicRegion', chechked: true },
        { label: '', value: 'coreRegion'},
        { label: '', value: 'internetRegion'}
    ];
    // input
    inputValue: string;
    titleFilter: FormControl = new FormControl();
    tabName: String = 'Middleware';
    changeTabName(tabName): void {
        this.tabName = tabName;
    }
    _console(value) {
        console.log(value);
    }
    _log(value) {
        console.log(value);
    }
    groupidHandler(event: any) {
        console.log('change event grouupid: ' + event);
    }

    constructor() { }

  ngOnInit() {
  }

}



