import {Injectable} from '@angular/core';

@Injectable()
export class ApplicationService {

    constructor() { }
    private applications: Application[] = [
        new Application(1, 'http://placehold.it/320x150', 'OA' , true, new Date()),
        new Application(2, 'http://placehold.it/320x150', '' , false, new Date()),
        new Application(3, 'http://placehold.it/320x150', 'NPS' , true, new Date()),
        new Application(4, 'http://placehold.it/320x150', '444' , true, new Date())
    ];
    private applicationMsgs: ApplicationMsg[] = [
        new ApplicationMsg(1, 'OA', 'v1()', new Date(), '', ''),
        new ApplicationMsg(2, '', 'v2()', new Date(), '', ''),
        new ApplicationMsg(3, 'NPS', 'v3()', new Date(), '', ''),
        new ApplicationMsg(4, '444', 'v3()', new Date(), '', '')
    ];
    // 
    getApplications(): Application[] {
        return this.applications;
    }
    // id appId
    getApplicationByID(appId: number): Application {
        return this.applications.find((application: Application) => application.appId === +appId);
    }
    // item
    getApplication(title: string): Application {
        return this.applications.find((application: Application) => application.title === title);
    }
    // id appId
    getApplicationMsgByID(appId: number): ApplicationMsg {
        return this.applicationMsgs.find((applicationMsg: ApplicationMsg) => applicationMsg.appId === +appId);
    }
}

// 
export class Application {
    constructor(
        public appId: number,
        public imgUrl: string,
        public title: string,
        public isActive: boolean,
        public createTime: any
    ) {
    }
}
// 
export class ApplicationMsg {
    constructor(
      public appId: number,
      public title: string,
      public serviceVersion: string,
      public updateTime: any,
      public instanceDesc: string,
      public desc: string
    ) {
    }
}



